import React, { Component } from 'react';
import { ListItem, List } from 'framework7-react';


class FriendItem extends Component {
  onClick({ id, name, lastname, last_activity, photo }){
    const app = this.$f7;
    app.data.friend = {
      id,
      name,
      lastname,
      last_activity,
      photo
    }
  }
  render(){
    const { id, name, lastname, last_activity, photo } = this.props
    return (
    <List mediaList>
      <ListItem
        onClick={this.onClick.bind(this, this.props)}
        title={ name + ' ' + lastname }
        after={ last_activity.slice(14) }
        subtitle="Last messages not found."
        link={'/dialog/' + id }
      >
      <img slot="media" alt={'user-' + id} src={'https://randomuser.me/api/portraits/' + photo} className="user_img" />
    </ListItem>
    </List>
    )
  }
}

export default FriendItem;



