import React, { Component } from 'react';
import { 
  Page, 
  Navbar,
  NavTitle,
  NavRight, 
  Messagebar, 
  Link, 
  MessagebarAttachments,
  MessagebarAttachment,
  MessagebarSheet, 
  MessagebarSheetImage,
  Messages,
  MessagesTitle,
  Message,
  Block,
  Preloader,
  Icon,
 } from 'framework7-react';




class Dialog extends Component {
  constructor(props) {
    super(props);

    this.state = {
      attachments: [],
      sheetVisible: false,
      typingMessage: null,
      loader: true,
      messagesData: [],
      images: [
        'http://lorempixel.com/300/300/cats/1/',
        'http://lorempixel.com/200/300/cats/2/'
      ],
      people: [
        {
          name: 'Kate Johnson',
          avatar: 'http://lorempixel.com/100/100/people/9',
        }
      ],
      answers: [
        'Yes!',
        'No',
        'Hm...',
        'I am not sure',
        'And what about you?',
        'May be ;)',
        'Lorem ipsum dolor sit amet, consectetur',
        'What?',
        'Are you sure?',
        'Of course',
        'Need to think about it',
        'Amazing!!!',
      ],
      responseInProgress: false,
    }
  }




  render() {
    return (
      <Page>
        <Navbar backLink=" ">
            <NavTitle>
              Айсулу Любимая
              <p className="online_user">онлайн</p>
            </NavTitle>
            <NavRight>
                  <img src="https://www.civilsociety.co.uk/assets/img/user-img-lg.jpg" className="chat_avatar" />
                 <Icon f7="more_vertical_fill" size="24px" className="mlI"></Icon>
            </NavRight>
        </Navbar>


        <Messagebar
          placeholder={this.placeholder}
          ref={(el) => {this.messagebarComponent = el}}
          attachmentsVisible={this.attachmentsVisible}
          sheetVisible={this.state.sheetVisible}
        >
          <Link
            iconIos="f7:camera_fill"
            iconMd="material:camera_alt"
            slot="inner-start"
            onClick={() => {this.setState({sheetVisible: !this.state.sheetVisible})}}
          ></Link>
          <Link
            iconIos="f7:arrow_up_fill"
            iconMd="material:send"
            slot="inner-end"
            onClick={this.sendMessage.bind(this)}
          ></Link>
          <MessagebarAttachments>
            {this.state.attachments.map((image, index) => (
              <MessagebarAttachment
                key={index}
                image={image}
                onAttachmentDelete={() => this.deleteAttachment(image)}
              ></MessagebarAttachment>
            ))}
          </MessagebarAttachments>
          <MessagebarSheet>
            {this.state.images.map((image, index) => (
              <MessagebarSheetImage
                key={index}
                image={image}
                checked={this.state.attachments.indexOf(image) >= 0}
                onChange={this.handleAttachment.bind(this)}
              ></MessagebarSheetImage>
            ))}
          </MessagebarSheet>
        </Messagebar>

        {
          this.state.loader ?
          <Messages ref={(el) => {this.messagesComponent = el}}>
          <MessagesTitle><b>Sunday, Feb 9,</b> 12:58</MessagesTitle>

          {this.state.messagesData.map((message, index) => (
            <Message
              key={index}
              type={message.type}
              image={message.image}
              name={message.name}
              first={this.isFirstMessage(message, index)}
              last={this.isLastMessage(message, index)}
              tail={this.isTailMessage(message, index)}
            >
              {message.text && (
                <span slot="text" dangerouslySetInnerHTML={{__html: message.text}} />
              )}
            </Message>
          ))}
          {this.state.typingMessage && (
            <Message
              type="received"
              typing={true}
              first={true}
              last={true}
              tail={true}
              header={`${this.state.typingMessage.name} набирает сообщение`}
              avatar={this.state.typingMessage.avatar}
            ></Message>
          )}
        </Messages>
          :
          <Messages>
            <Block className="text-align-center">
              <Preloader color="multi"></Preloader>
            </Block>
          </Messages>
        }
      </Page>
    )
  }

  get attachmentsVisible() {
    const self = this;
    return self.state.attachments.length > 0;
  }
  get placeholder() {
    const self = this;
    return self.state.attachments.length > 0 ? 'Добавить комментарий или Отправить' : 'Сообщения';
  }
  componentDidMount() {
    const self = this;
    const app = self.$f7;
    const socket = app.data.socket;

    console.log("DATA GLOBAL ", socket);
    socket.on('meSend', this.onMeSend.bind(this));


    // GET Dialog
    this.$request.get('http://otau.org/main/dialog_cors', { foo:'bar', id:5 }, function (data) {
        var dataParse = JSON.parse(data);
        console.log("dataParse ", dataParse);

        if (typeof dataParse === 'object'){
          for(let i = 0; i < dataParse.length; i++){
            if (dataParse[i].otkogo_id === app.data.friend.id){
              self.setState({ messagesData: [...self.state.messagesData, {
                text: dataParse[i].msg,
                type: 'received',
                name: app.data.friend.name + ' ' + app.data.friend.lastname
              }],
                typingMessage: null,
                responseInProgress: false
              });
            }else{
              self.setState({ messagesData: [...self.state.messagesData, {
                text: dataParse[i].msg,
                type: 'sent',
                name: app.data.user.name + ' ' + app.data.user.lastname
              }],
                typingMessage: null,
                responseInProgress: false
              });
            }
          }
        }
    }); // GET DATA-MSG

    self.$f7ready(() => {
      self.messagebar = self.messagebarComponent.f7Messagebar;
      self.messages = self.messagesComponent.f7Messages;
    });
  }
  isFirstMessage(message, index) {
    const self = this;
    const previousMessage = self.state.messagesData[index - 1];
    if (message.isTitle) return false;
    if (!previousMessage || previousMessage.type !== message.type || previousMessage.name !== message.name) return true;
    return false;
  }
  isLastMessage(message, index) {
    const self = this;
    const nextMessage = self.state.messagesData[index + 1];
    if (message.isTitle) return false;
    if (!nextMessage || nextMessage.type !== message.type || nextMessage.name !== message.name) return true;
    return false;
  }
  isTailMessage(message, index) {
    const self = this;
    const nextMessage = self.state.messagesData[index + 1];
    if (message.isTitle) return false;
    if (!nextMessage || nextMessage.type !== message.type || nextMessage.name !== message.name) return true;
    return false;
  }
  deleteAttachment(image) {
    const self = this;
    const attachments = self.state.attachments;
    const index = attachments.indexOf(image);
    attachments.splice(index, 1);
    self.setState({ attachments });
  }
  handleAttachment(e) {
    const self = this;
    const attachments = self.state.attachments;
    const index = self.$$(e.target).parents('label.checkbox').index();
    const image = self.state.images[index];
    if (e.target.checked) {
      // Add to attachments
      attachments.unshift(image);
    } else {
      // Remove from attachments
      attachments.splice(attachments.indexOf(image), 1);
    }
    self.setState({ attachments });
  }
  sendMessage() {
    const self = this;
    const app = self.$f7;
    const socket = app.data.socket;


    const text = self.messagebar.getValue().replace(/\n/g, '<br>').trim();
    const messagesToSend = [];
    self.state.attachments.forEach((attachment) => {
      messagesToSend.push({
        image: attachment,
      });
    });
    if (text.trim().length) {
      messagesToSend.push({
        text,
      });

      let msg = {
        uid: app.data.friend.id,
        msg: text
      }
  
      socket.emit('meSend', msg);

    }
    if (messagesToSend.length === 0) {
      return;
    }

    self.setState({
      // Reset attachments
      attachments: [],
      // Hide sheet
      sheetVisible: false,
      // Send message
      messagesData: [...self.state.messagesData, ...messagesToSend],
    });
    self.messagebar.clear();

    // Focus area
    if (text.length) self.messagebar.focus();

    // Mock response
    if (self.state.responseInProgress) return;
    self.setState({
      responseInProgress: true,
    })

    

    

    // setTimeout(() => {
   
    //   self.setState({
    //     typingMessage: {
    //       name: person.name,
    //       avatar: person.avatar,
    //     },
    //   });

    // //   setTimeout(() => {
    // //     self.setState({
    // //       messagesData: [...self.state.messagesData, {
    // //         text: answer,
    // //         type: 'received',
    // //         name: person.name,
    // //         avatar: person.avatar,
    // //       }],
    // //       typingMessage: null,
    // //       responseInProgress: false,
    // //     });
    // //   }, 4000);
    // }, 1000);
  }


  onMeSend(e){



    console.log("data Fake self ", e);
    const app = this.$f7;

    setTimeout(()=>{
      this.setState({
        typingMessage: {
          name: app.data.friend.name
        },
      });

      setTimeout(()=>{
        this.setState({
          // Reset attachments
          attachments: [],
          // Hide sheet
          sheetVisible: false,
          // Send message
          messagesData: [...this.state.messagesData, {
            text: e.msg,
            type: 'received',
            name: app.data.friend.name + ' ' + app.data.friend.lastname
          }],
          typingMessage: null,
          responseInProgress: false,
        });
      },3000)
    },1000)




  }
}

export default Dialog;



