import React, { Component } from 'react';
import { Page,  List, ListButton, ListItem, Input, Label } from 'framework7-react';

// import { httpAuth } from './../../configUrl'


class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      phone: '',
      password: '',
    };
  }

  render() {
    return (
      <Page noToolbar noNavbar noSwipeback loginScreen>
        <img src="images/logo.png" alt="logo" className="logo" />
        <List form>
          <ListItem>
            <Label>Телефон</Label>
            <Input 
              required 
              validate 
              pattern="[0-9]*"
              info='Введите любой номер телефона для тестового входа' 
              errorMessage="Только цифры"
              type="text" 
              placeholder="Введите телефон"
              maxlength='11'
              minlength='3'
              onInput={(e) => {
              this.setState({ phone: e.target.value});
            }}></Input>
          </ListItem>
          <ListItem>
            <Label>Пароль</Label>
            <Input type="password" placeholder="Введите пароль любой" onInput={(e) => {
              this.setState({ password: e.target.value});
            }} className="borderB"></Input>
          </ListItem>
        </List>
        <List>
          <ListButton onClick={this.signIn.bind(this)} className="auth" >ВОЙТИ</ListButton>
          <ListButton link="/register/" className="registr">РЕГИСТРАЦИЯ</ListButton>
          <ListItem link="#" title="Забыли пароль?" view="#main-view" panelClose></ListItem>
          {/* <ListItem link="/register2/" title="Test Register" view="#main-view" panelClose></ListItem> */}
        </List>
      </Page>
    )
  }
  signIn() {
    const self = this;
    // const app = self.$f7;
    const router = self.$f7router;

    let data = this.state;

    // this.$request.setup({
    //   contentType: 'application/json'
    // })

    if(data.phone != '' && data.password != ''){
      router.navigate("/");
    }

    // this.$request.post(httpAuth + "/auth", JSON.stringify(data), function (data) {
    //   var dataParse = JSON.parse(data);

    //   console.log("Auth data ", dataParse)

    //   if (dataParse.success){
    //     app.data.token = dataParse.success.result.token;
    //     router.navigate("/");
    //   }

    //   if (dataParse.error){
    //     console.log("router", app.data.token = dataParse);
    //     app.dialog.alert(`${dataParse.error.message}`, () => {
    //       // router.back();
    //     });
    //   }

    // });


  }
}

export default Login;



