import React, { Component } from 'react';
import {
    Page,
    Navbar,
    NavTitle,
    NavRight,
    Link,
    Toolbar,
    Tabs,
    Tab,
    Icon,
    List,
    ListItem,
    Subnavbar,
    Searchbar,
    Popover,
    Button
  } from 'framework7-react';
  
  import Friend from './Friend'
  import Socket from './../../socket'

const DATA_FRIENDS = {
  "friends": [
    {
      "id": "4",
      "name": "Тимур",
      "lastname": "Артамов",
      "photo": "men/80.jpg",
      "status": "1",
      "last_activity": "2018-07-11 04:45:50"
    },
    {
      "id": "5",
      "name": "Марат",
      "lastname": "Журмухамедов",
      "photo": "men/93.jpg",
      "status": "1",
      "last_activity": "2018-07-11 04:45:50"
    },
    {
      "id": "6",
      "name": "Асет",
      "lastname": "Габдуллов",
      "photo": "men/93.jpg",
      "status": "1",
      "last_activity": "2018-07-11 04:45:50"
    },
    {
      "id": "7",
      "name": "actor",
      "lastname": "actor",
      "photo": "women/82.jpg",
      "status": "1",
      "last_activity": "2018-07-11 07:59:52"
    }],
  "user": {
    "id": "5",
    "name": "Марат",
    "lastname": "Журмухамедов"
  }
};




class HomePage extends Component {
  constructor(props){
    super(props);
    this.state = {
      friends: [],
      user: [],
      isLoading: false
    }
  }

  componentDidMount(){
    const self = this;
    const app = self.$f7;

    this.setState({ isLoading: true })

    setTimeout(()=>{
      this.setState(
        { friends: DATA_FRIENDS.friends,
      user: DATA_FRIENDS.user, isLoading: false })
    }, 1500)

    // Socket
    let socket = this.socket = new Socket();
    socket.on('Connect', this.onConnect.bind(this));
    socket.on('Disconnect', this.onDisconnect.bind(this));
    // socket.on('uid', this.onAddChannel.bind(this));
    app.data.socket = socket;

    // GET Friends
    // this.$request.get('http://otau.org/main/cors', { foo:'bar', id:5 }, function (data) {
    //     var dataParse = JSON.parse(data);
    //     self.setState({ friends: dataParse.friends, user: dataParse.user, isLoading: false })
    //     app.data.user = dataParse.user;
    //   });

  }

  onConnect(){
    this.setState({ connected : true})
    console.log("WS connect...")
  }
  onDisconnect(){
    this.setState({ connected : true})
  }



  render(){
    const { friends, isLoading } = this.state

    return (
      <Page>
        <Navbar>
          <NavTitle>
               <p className="name_navbar">Чаты</p>
          </NavTitle>
          <NavRight>
               <Button  popoverOpen=".popover-menu"> <Icon f7="more_vertical_fill" size="24px" className="mlI"></Icon></Button>
          </NavRight>
          <Subnavbar inner={false}>
            <Searchbar
              searchContainer=".search-list"
              searchIn=".item-title"
              disableButtonText=""
              placeholder="Поиск"
            ></Searchbar>
          </Subnavbar>
        </Navbar>
        <Tabs>
          <Tab id="tab-1" tabActive>
                  <List mediaList>
                  { isLoading ?
                    <p> Загрузка....</p>
                    :
                    friends.map( friend => <Friend key={friend.id} {...friend} />)
                  }
                </List>
          </Tab>
          <Tab id="tab-2">
                  
              
                <List className="searchbar-not-found">
                  <ListItem title="Нечего не найдено" />
                </List>  
                
                <List className="search-list searchbar-found">
                  <ListItem title="Acura" />
                  <ListItem title="Audi" />
                  <ListItem title="BMW" />
                  <ListItem title="Cadillac " />
                  <ListItem title="Chevrolet " />
                  <ListItem title="Chrysler " />
                  <ListItem title="Dodge " />
                  <ListItem title="Ferrari " />
                  <ListItem title="Ford " />
                  <ListItem title="GMC " />
                  <ListItem title="Honda" />
                  <ListItem title="Hummer" />
                  <ListItem title="Hyundai" />
                  <ListItem title="Infiniti " />
                  <ListItem title="Isuzu " />
                  <ListItem title="Jaguar " />
                  <ListItem title="Jeep " />
                  <ListItem title="Kia" />
                  <ListItem title="Lamborghini " />
                  <ListItem title="Land Rover" />
                  <ListItem title="Lexus " />
                  <ListItem title="Lincoln " />
                  <ListItem title="Lotus " />
                  <ListItem title="Mazda" />
                  <ListItem title="Mercedes-Benz" />
                  <ListItem title="Mercury " />
                  <ListItem title="Mitsubishi" />
                  <ListItem title="Nissan " />
                  <ListItem title="Oldsmobile " />
                  <ListItem title="Peugeot " />
                  <ListItem title="Pontiac " />
                  <ListItem title="Porsche" />
                  <ListItem title="Regal" />
                  <ListItem title="Saab " />
                  <ListItem title="Saturn " />
                  <ListItem title="Subaru " />
                  <ListItem title="Suzuki " />
                  <ListItem title="Toyota" />
                  <ListItem title="Volkswagen" />
                  <ListItem title="Volvo" />
                </List>
                      
          </Tab>
        </Tabs>
        <Toolbar tabbar labels bottomMd={true}>
          <Link tabLink="#tab-1" tabLinkActive text="Чаты" iconIos="f7:chat" iconMd="material:chat"></Link>
          <Link tabLink="#tab-2" text="Контакты" iconIos="f7:person" iconMd="material:person"></Link>
        </Toolbar>
        
        <Popover className="popover-menu">
          <List>
            <ListItem link="/login/" popoverClose title="Выйти" />
          </List>
        </Popover>
      </Page>
    )
  }
}


export default HomePage;


