import React, { Component } from 'react';
import { Page,  List, ListButton, ListItem, Input, Label } from 'framework7-react';

import { httpAuth } from './../../configUrl'


class Register2 extends Component {
  constructor(props) {
    super(props);

    this.state = {
      password: '',
      confirm_password: '',
      phone: '',
      code: '',
      login: '',
      success: false
    };
  }

  signInCode(){
    const self = this;
    const app = self.$f7;
    // const router = self.$f7router;

    this.$request.setup({
      contentType: 'application/json'
    })


    let data = {
      phone: this.state.phone,
      code: this.state.code
    }

    console.log("send CODE SMS ", data);
  
    this.$request.post(httpAuth + "/verify-phone", JSON.stringify(data), function (data) {
      var dataParse = JSON.parse(data);

      console.log("Regist data ", dataParse)

      if (dataParse.success){

        app.dialog.alert(`OK`, () => {
          // router.navigate("/verify-phone/");
        });
      }

      if (dataParse.error){
        app.dialog.alert(`${dataParse.error.message}`, () => {
          // router.back();
        });
      }

    });
  }

  render() {
    return (
      <Page noToolbar noNavbar noSwipeback loginScreen>
      <img src="images/logo.png" alt="logo" className="logo" />
      {
        this.state.successPhone ?
        <List form>
          <ListItem>
            <Label>Code SMS-2</Label>
            <Input 
              value={this.state.code}
              // onChange={this.onChangeCode.bind(this)}
              required
              validate 
              pattern="[0-9]*"
              info='Введите код-смс'
              errorMessage="Only numbers please!"
              type="tel" 
              placeholder="Введите код-СМС"
              maxlength='11'
              minlength='4'
              className="borderB"></Input>
          </ListItem>
            <ListButton onClick={this.signInCode.bind(this)}  className="auth">Подтвердить Код СМС</ListButton>
            <ListButton link="/login/"  className="auth mt">НАЗАД</ListButton>
        </List>
          :
        <List form>
          <ListItem>
            <Label>Пароль</Label> 
            <Input
              required 
              validate 
              info='Введите номер телефона без +' 
              errorMessage="Заполните поле"
              type="text"
              placeholder="Введите пароль"
              maxlength='20'
              minlength='8'
              onChange={this.onChangePassword.bind(this)}
              value={this.state.password}
              className="borderB"></Input>
          </ListItem>
          <ListItem>
            <Label>Повторите пароль</Label> 
            <Input
              required 
              validate 
              info='' 
              errorMessage="Заполните поле"
              type="text"
              placeholder="Повторите пароль"
              maxlength='20'
              minlength='8'
              onChange={this.onChangeConfirmPassword.bind(this)}
              value={this.state.confirm_password}
              className="borderB"></Input>
          </ListItem>
          <ListButton onClick={this.signIn.bind(this)} className="auth">РЕГИСТРАЦИЯ</ListButton>
          <ListButton link="/login/"  className="auth mt">НАЗАД</ListButton>
        </List>
      }
  </Page>
    )
  }

  onChangeConfirmPassword(e){
    this.setState({
      confirm_password: e.target.value.trim()
    })
  }

  onChangePassword(e){
    this.setState({
      password: e.target.value.trim()
    })
  }

  signIn() {

    const self = this;
    const app = self.$f7;
    const router = self.$f7router;

   
    this.$request.setup({
      contentType: 'application/json',
      headers: {
        'Authorization': 'Bearer ' + app.data.token
      }
    })

    if (this.state.password === this.state.confirm_password){
      let data = {
        password: this.state.password,
        confirm_password: this.state.confirm_password
      }
      console.log("send PHONE ", data);
  
      this.$request.post(httpAuth + "set-password", JSON.stringify(data), function (data) {
        var dataParse = JSON.parse(data);
  
        if (dataParse.success){
  
          self.setState({ success: true })
  
          app.dialog.alert(`OK`, () => {  
            router.navigate("/login/");
            console.log("Regist data ", dataParse)
          });
        }
  
        if (dataParse.error){
          app.dialog.alert(`${dataParse.error.message}`, () => {
            // router.back();
          });
        }
  
      });
    } else {
      app.dialog.alert(`пароли не верны!`, () => {
        // router.back();
      });
    }
   



    }


}

export default Register2;


